
.group_top{background-image: url("../../assets/images/ic_group_banner.png");width: 100%;height: 150px;background-size: cover;position: relative;text-align: center;margin-top: 70px;
}
.group_data{font-size: 16px;color: #666;padding-top: 50px;
}
.group_data em{font-style: normal;
}
.group_small_title{font-size: 12px;color: #999;
}
.group_top span{position: absolute;left:20px;bottom: 0;
}
.group_mid{padding: 10px;position: relative;
}
.group_mid_img{width: 100%;height: 100px;
}
.group_mid_text{position: absolute;left: 20px;bottom: 40px;font-size: 14px;color: #fff;
}
.group_mid_border{height: 10px;border-bottom: 1px solid lightgray;position: relative;
}
.group_mid_border p{position: absolute;width:60px;height: 20px;background: #fff;color: #666;font-size: 14px;left:150px;top: 0;
}
.group_bottom{width: 100%;padding:10px 20px;
}
.group_bottom li{width: 100%;
}
.group_bottom_list{position: relative;height: 75px;
}
.group_bottom_list h5{float: left;padding-left: 10px;
}
.group_bottom_list p{float: left;font-size: 12px;color: #ddd;padding-left: 10px;width: 200px;text-align: left;
}
.group_bottom_left_img{width:50px;height: 50px;float: left;
}
.group_bottom_right_img{width: 20px;height: 20px;position: absolute;right: 30px;top:15px;
}
.group_bottom_list span em{font-style: normal;
}
.group_bottom_list span {font-size: 12px;color: #ddd;position: absolute;right: 60px;top:16px;
}



