}
.scan{width: 25px;position: absolute;left: 280px;top:26px;z-index: 10
}
.chat{width: 35px;position: absolute;left: 325px;top:20px;
}
.radio_search,.news_search{width:35px;position: absolute;left:260px;top:20px;
}
.radio_chat,.news_chat,.mine_chat{width: 35px;position: absolute;left:320px;top:20px;
}



