
.misic{text-align: left;background: #f7f7f7;width: 100%;padding-top: 100px;
}
.misic_top{background: #fff;padding:15px 20px;border-top: 17px solid #f7f7f7;
}
.misic_top h3{font-size: 16px;color: #666;float: left;width: 100%;height: 30px;line-height: 30px;
}
.misic_top h3 span{float: right;color: #42bd56;font-weight: normal;
}
.misic_top_list {width: 100%;clear: both;height: 190px;
}
.misic_top_list li{float: left; padding-right:16px;
}
.misic_top_list .last_li{padding-right: 0;
}
.misic_top_list img{width:100px;height: 122px;
}
.misic_top_list p{font-size: 14px;
}
.misic_top_list i{font-style: normal;width: 15px;height: 15px;float: left;
}
.misic_top_list i img{width: 15px;height: 15px;
}



