
.film{text-align: left;background: #f7f7f7;width: 100%;padding-bottom: 70px;padding-top: 100px;
}
.new_book,.looking{background: #fff;padding:15px 20px;border-top: 17px solid #f7f7f7;
}
.new_book h3,.looking h3{font-size: 16px;color: #666;float: left;width: 100%;height: 30px;line-height: 30px;
}
.new_book h3 span ,.looking h3 span{float: right;color: #42bd56;font-weight: normal;
}
.new_book_list {width: 100%;clear: both;height: 190px;
}
.new_book_list li{float: left; padding-right:17px;
}
.new_book_list .last_li{padding-right: 0;
}
.new_book_list img{width:100px;
}
.new_book_list p{font-size: 14px;
}
.new_book_list i{font-style: normal;width: 15px;height: 15px;float: left;
}
.new_book_list i img{width: 15px;
}
.looking h3{font-size: 14px;
}




