
.television{text-align: left;background: #f7f7f7;width: 100%;padding-top: 100px;
}
.new_televi,.update_televi{background: #fff;padding:15px 20px;border-top: 17px solid #f7f7f7;
}
.new_televi h3,.update_televi h3{font-size: 16px;color: #666;float: left;width: 100%;height: 30px;line-height: 30px;padding-bottom: 10px;
}
.new_televi h3 span ,.update_televi h3 span{float: right;color: #42bd56;font-weight: normal;
}
.new_televi_list {width: 100%;clear: both;height: 190px;
}
.new_televi_list li{float: left; padding-right:17px;
}
.new_televi_list .last_li{padding-right: 0;
}
.new_televi_list img,.update_televi_list img{width:100px;
}
.new_televi_list p{font-size: 14px;
}
.new_televi_list i{font-style: normal;width: 15px;height: 15px;float: left;
}
.new_televi_list i img,.update_televi_list i img{width: 15px;padding-right: 0
}
.update_televi_list{clear: both;
}
.update_televi_list img {float: left;padding-right: 20px;width: 70px;
}
.update_televi_list i{font-size: 16px;
}
.update_televi_list li{height: 100px;border: 1px solid lightgray;padding:10px;
}
.update_televi_list p{line-height: 30px;
}




