
.city{text-align: left;padding-top: 100px;
}
.city_banner_top{background: #fff;width: 100%;border-top: 10px solid #f7f7f7;padding-top: 10px;
}
.city_banner_top span{font-size: 12px;color: lightgray;padding-left: 20px;
}
.city_banner_top span em{color: #42bd56;font-style: normal;
}
.city_banner_top i{padding-left: 20px;color: lightgray;
}
.city_banner_mid{ width: 100%;border-top:10px solid #f7f7f7;
}
.city_banner_mid img{ width: 100%;height: 100px;
}
.city_banner_mid h4{font-size: 16px;color: #666;padding-left: 10px;height: 30px;line-height: 30px;
}
.city_banner_mid p{font-size: 12px;color:lightgray;padding-left: 10px;height:20px;line-height:20px;
}
.city_hot{width: 100%;border-top: 10px solid #f7f7f7;
}
.city_hot h3{font-size: 16px;color: #666;float: left;width: 100%;height: 30px;line-height: 30px;padding-bottom: 10px;padding-left: 20px;
}
.city_hot h3 span {float: right;color: #42bd56;font-weight: normal;padding-right: 40px;
}
.city_hot_list li{width: 100px;height: 120px;float: left;padding-left: 18px;
}
.city_hot_list i{float: left;border: 1px solid red;color: red;font-size: 8px;
}
.city_hot_list p{font-size: 14px;color: #666;
}
.city_hot_list i img{padding-right: 0;width: 50px;
}
.city_hot_list img {float: left;padding-right: 20px;width: 100px;
}



