
.main h2{color:orange;font-size: 16px;
}
.main{padding: 10px 20px;text-align: left;padding-bottom: 80px;
}
.main li{border-bottom: 1px solid lightgray;
}
.clear::after{content:'';display: block;clear: both;
}
.main a{text-decoration: none;
}
.left_text{width: 60%;float: left;
}
.left_text h3{color: #000;text-align: left;font-size: 16px;line-height: 25px;padding-top: 10px;
}
.left_text p{color: gray;text-align: left;font-size: 12px;line-height: 18px;
}
.left_text i{font-style: normal;width: 20px;color: lightgray;
}
.left_text span{line-height: 30px;height:30px;color: lightgray;
}
.left_text em{font-style: normal;
}
.right_Img{width: 20%;float: right;padding: 20px;
}



